.statistics {
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  border-radius: 10px;
  overflow: hidden;

  & + .statistics {
    margin-top: 15px;
  }
}

.title {
  display: block;
  padding-top: 20px;
  padding-bottom: 20px;
  text-align: center;
  background-color: lightcyan;
}

$count: 15;

.statsList {
  display: flex;
  justify-content: space-between;
  border-top: 1px solid lightgrey;
}

@for $i from 1 through $count {
  .item {
    &:nth-child(#{$i}) {
      display: flex;
      flex-direction: column;
      align-items: center;
      width: calc(100% / 5);
      padding-top: 10px;
      padding-bottom: 10px;
      border-left: 1px solid lightgrey;
      background-color: rgb(random(255), random(255), random(255));
    }
  }
}

.label {
  display: block;
  font-size: 14px;
  margin-bottom: 5px;
}

.percentage {
  display: block;
  font-size: 18px;
}

.percentage {
  font-weight: 700;
}
